.waterfall{position:relative}.waterfall>.item{position:absolute;width:20%;padding:0 15px;margin-bottom:15px}.waterfall>.item .inner{background:#fff;border-radius:3px}.waterfall>.item .inner .cover img{border-radius:3px 3px 0 0;width:100%}.waterfall>.item .inner .info{border-top:1px solid #efefef;padding:15px 10px}.waterfall>.item .inner .info .avatar img{width:24px;height:24px;border-radius:50%}.waterfall>.item .inner .info .avatar strong{margin-left:4px;color:#757373}.clearFloat{zoom:1}.clearFloat::after{display:block;clear:both;content:'';visibility:hidden;height:0}
