ckground:#4db973}.recommend-article .right{display:flex;flex-wrap:wrap;flex:2;background:#bad3d6}.recommend-article .right .article{display:flex;flex-direction:column;flex:1 1 200px;padding:10px}.recommend-article .right .article-s .cover{height:100px;flex:1;overflow:hidden}.recommend-article .right .article-s .cover img{width:100%;height:100%;transform:scale(1);transition:1s all ease-out}.recommend-article .right .article-s .cover:hover img{transform:scale(1.1);transition:1s all ease-in}.recommend-article .right .article-s .title{padding:10px;background:#ccc}.recommend-article .right .article-s:last-child{position:relative;padding-top:0}.recommend-article .right .article-s:last-child .title{position:absolute;bottom:10px;width:calc(100% - 20px);background:rgba(0,0,0,0.5);color:#fff}
